/* footer
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/" as *;

.footer {
  background-color: $bg-color;
}

.footer__inner {
  padding: 50px 20px;
  text-align: center;

  @include mq(md) {
    padding: 70px 20px;
  }

  & .copyright {
    font-size: rem(11);
    font-weight: bold;
    color: $c_white;
    letter-spacing: 0.08em;

    @include mq(md) {
      font-size: rem(14);
    }
  }
}
