/* ボタン
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.btn {
  display: inline-block;
  width: 180px;
  max-width: 100%;
  padding: 20px 0;
  font-size: rem(16);
  font-weight: bold;
  line-height: 1;
  color: #fff;
  text-align: center;
  text-decoration: none;
  background-image: $btn-gradient;
  border-radius: 50px;
  box-shadow: 0 6px 0 #dd118e;
  transition: 0.25s;

  @include mq(md) {
    width: 300px;
    padding: 24px 10px;
    font-size: rem(18);
    box-shadow: 0 10px 0 #dd118e;
  }
}

.btn:hover {
  box-shadow: 0 6px 0 #dd118e;
  transform: translateY(4px);
}
