/* about
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.about {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mq(md) {
    flex-direction: row;
  }
}

.about__img-wrapper {
  width: 100%;
  margin-right: 0;
  margin-bottom: 20px;

  @include mq(md) {
    flex: 0 1 40%; //画像サイズの幅をレイアウト幅で割る
    margin-right: 9.33333%; //画像間の空白をレイアウト幅で割る
  }
}

.about__img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: center 38%;

  @include mq(md) {
    height: auto;
    object-fit: fill;
    object-position: 50% 50%;
  }
}

.about__body {
  flex: 1;
}

//要素が増減した時の余白の制御
.about__body > *:last-child {
  margin-bottom: 0;
}

.about__subtitle {
  margin-bottom: 16px;
  font-size: rem(18);

  @include mq(md) {
    margin-bottom: 20px;
    font-size: rem(24);
  }
}

.about__title {
  margin-bottom: 25px;
  font-size: rem(40);
  font-weight: bold;
  line-height: 1;
  color: $c_key1;
  letter-spacing: 0.1em;

  @include mq(md) {
    margin-bottom: 40px;
    font-size: rem(48);
  }
}

.about__text {
  margin-bottom: 15px;
  font-size: rem(16);
  line-height: 1.6;

  @include mq(md) {
    margin-bottom: 20px;
    font-size: rem(18);
    line-height: 1.8;
  }
}

.about__info-wrapper {
  margin-bottom: 20px;
}

.about__info {
  display: flex;
  padding: 5px 0;
  font-size: rem(15);
}

.about__info-date {
  flex: 0 0 15%;
}

.about__info-text {
  flex: 1 1 auto;
}

.about__btn {
  width: 230px;
  font-size: rem(18);
}
