/* speaker
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.speaker {
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;

  @include mq(md) {
    flex-direction: row;
  }

  &:last-child {
    padding-bottom: 0;
  }
}

.speaker--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
  }
}

.speaker--reverse .speaker__img-wrapper {
  margin-right: 0;
}

.speaker__img-wrapper {
  width: 100%;
  margin-right: 0;
  margin-bottom: 20px;

  @include mq(md) {
    flex: 0 1 40%; //画像サイズの幅をレイアウト幅で割る
    margin-right: 5.12%; //画像間の空白をレイアウト幅で割る
  }
}

.speaker--reverse .speaker__body {
  margin-right: 5.12%;
}

.speaker__img {
  width: 100%;
}

.speaker__body {
  flex: 1;
}

//要素が増減した時の余白の制御
.speaker__body > *:last-child {
  margin-bottom: 0;
}

.speaker__name {
  margin-bottom: 30px;

  @include mq(md) {
    margin-bottom: 40px;
  }
}

.speaker__name-main {
  margin-bottom: 5px;
  font-size: rem(32);
  line-height: 1;
  color: $c_key1;

  @include mq(md) {
    font-size: rem(42);
  }
}

.speaker__name-sub {
  font-size: rem(14);
  font-weight: bold;

  @include mq(md) {
    font-size: rem(18);
  }
}

.speaker__text {
  margin-bottom: 15px;
  font-size: rem(14);
  line-height: 1.6;

  @include mq(md) {
    margin-bottom: 20px;
    font-size: rem(16);
    line-height: 1.8;
  }
}
