/* ----------------------------------------
	Form Parts
---------------------------------------- */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

/* 各フォーム部品の基本スタイルを上書きします */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
textarea,
select {
  /* ブラウザ標準スタイルを無効にする */
  width: 100%;
  padding: 8px;
  font-size: rem(16);
  background-color: $c_white;
  border: 1px solid #aaa;
  border-radius: 5px;
  appearance: none;
  appearance: none;
}

.form-container {
  padding: 8%;
  background-color: #1a1a1a;
  border: 5px solid $c_key1;
  border-radius: 20px;

  @include mq(md) {
    padding: 90px;
  }
}

.form__item {
  margin-bottom: 30px;
}

.form__item:nth-last-child(2) {
  margin-bottom: 50px;
}

.form__ttl {
  display: inline-block;
  margin-bottom: 10px;
  font-weight: bold;
}

.form__body {
  width: 100%;
}

input[type="radio"] + span {
  padding-left: 5px;
  margin-right: 35px;
}

.form__textarea {
  min-width: 100%;
  min-height: 200px;

  @include mq(md) {
    min-width: 600px;
  }
}

.form__btn {
  text-align: center;
}

/* 入力フィールド
--------------------------------- */
.inputField {
  @include mq(md) {
    width: 300px;
  }
}

.inputField--tel {
  @include mq(md) {
    width: 200px;
  }
}

/* 補足説明文
--------------------------------- */
.form__note {
  margin-left: 5px;
  font-size: rem(12);
}

/* 水平ラジオボタン選択肢リスト
--------------------------------- */
.radioList {
  @include mq(md) {
    display: flex;
    align-items: center;
  }
}

/* セレクトボックス
------------------------------------ */
select {
  display: block;
  width: 100%;
  padding: 10px 30px 8px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  appearance: none;
  appearance: none;
}

/* 必須・任意ラベル
--------------------------------- */
.label-required {
  margin-left: 10px;
  font-size: rem(12);
  color: $c_key1;
}
