/* mv
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.mv {
  position: relative;
  overflow: hidden;
  background-image: $bg-gradient;
}

.mv__inner {
  display: flex;
  max-width: 1240px;
  height: 450px;
  padding: 0 30px;
  margin: 0 auto;

  @include mq(md) {
    height: 700px;
    padding: 0 50px;
  }
}

.mv__title-area {
  @include z-index(default);

  padding-top: 75px;

  @include mq(md) {
    padding-top: 156px;
  }
}

.mv__title-area::after {
  position: absolute;
  bottom: 35%;
  left: 48%;
  display: inline-block;
  width: 153px;
  height: 64px;
  content: "";
  background-image: url(../../images/teiji-de-kaerumon.svg);
  background-size: contain;

  @include mq(sm) {
    bottom: 29%;
    left: 51%;
  }

  @include mq(md) {
    bottom: 175px;
    left: 382px;
    width: 355px;
    height: 140px;
  }
}

.mv__sub-title {
  margin-bottom: 0.8em;
  font-weight: bold;
  line-height: 1.3;
  color: $c_key1;

  @include mq(md) {
    font-size: rem(24);
    line-height: 1;
  }
}

//タブレット幅以上では改行なし
.mv__sub-title-br {
  @include mq(md) {
    display: none;
  }
}

.mv__title {
  margin-bottom: 10px;
  font-size: clamp(rem(24), 10vw, rem(100));
  font-weight: bold;
  line-height: 1;
}

.mv__date {
  margin-bottom: 30px;
  font-size: rem(18);
  font-weight: bold;
  letter-spacing: 0.07em;

  @include mq(md) {
    font-size: rem(36);
  }
}

.mv__speaker-name {
  position: absolute;
  right: 20px;
  bottom: 20px;
  font-size: rem(12);
  line-height: 1;

  @include mq(md) {
    font-size: rem(21);
  }
}

.mv__speaker-name-label {
  font-size: rem(10);

  @include mq(md) {
    font-size: rem(12);
  }
}

.mv__img {
  position: absolute;
  top: 3px;
  right: -80px;
  width: 340px;

  @include mq(md) {
    top: -36px;
    right: 0;
    width: auto;
  }
}
