/* header
------------------------------------------------------ */
@use "../base/setting" as *;
@use "../global/" as *;

.header {}

.header__inner {
  @include z-index(header);

  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding: 20px;

  @include mq(md) {
    height: 90px;
    padding: 30px;
  }
}

.header__logo {
  font-size: rem(28);
  font-weight: bold;
  line-height: 1;
}

.header__link {
  color: $c_key1;
}

.header-nav {
  display: flex;
}

.header-nav__item {
  list-style: none;
}

.header-nav__link {
  margin-left: 15px;
  font-size: rem(12);
  font-weight: bold;
  color: $c_white;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  @include mq(md) {
    margin-left: 30px;
    font-size: rem(16);
  }
}
