/* page-top
------------------------------------------------------ */
@use "../../../base/setting" as *;
@use "../../../global/" as *;

.page-top {
  @include z-index(page-top);

  position: fixed;
  right: 20px;
  bottom: 20px;
  display: none;

  @include mq(md) {
    right: 40px;
    bottom: 40px;
  }
}

.page-top__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-image: $btn-gradient;
  border-radius: 50%;
  box-shadow: 0 0 10px 0 rgb(0 0 0 / 20%);
  transition: opacity 0.3s;

  @include mq(md) {
    width: 80px;
    height: 80px;
  }
}

.page-top__link::after {
  font-family: "Material Icons Round";
  font-size: rem(24);
  color: $c_white;
  content: "\e316";

  @include mq(md) {
    font-size: rem(40);
  }
}
