/* section
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global"as *;

.section__inner {
  max-width: 1170px;
  padding: 50px $padding-side-sp;
  margin: 0 auto;

  @include mq(md) {
    padding: 110px $padding-side;
  }
}

.section__title {
  margin-bottom: 20px;
  font-weight: bold;
  line-height: 1;
  text-align: center;

  @include mq(md) {
    margin-bottom: 60px;
  }
}

.section__title-main {
  margin-bottom: 5px;
  font-size: rem(32);
  line-height: 1;
  color: $c_key1;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  @include mq(md) {
    margin-bottom: 10px;
    font-size: rem(60);
  }
}

.section__title-sub {
  font-size: rem(14);
  letter-spacing: 0.05em;

  @include mq(md) {
    font-size: rem(20);
  }
}

.section-about,
.section-session {
  background-color: $bg-color;
}

.section-speaker {
  background-image: $bg-gradient;
}

.section-registor {
  background-image: url(../../images/bg.png);
  background-repeat: repeat;
}
