/* table
------------------------------------------------------ */
@use "../../../base/setting"as *;
@use "../../../global/"as *;

.table__inner {
  width: 100%;
  border-collapse: collapse;  /* セル同士の間に隙間を開けない */
}

.table__header {
  width: 80px;
  padding: 20px 0;
  font-size: rem(16);
  font-weight: bold;
  text-align: left;
  vertical-align: top;
  border-bottom: 1px solid $c_key1;

  @include mq(md) {
    width: 200px;
    padding: 40px 30px;
    font-size: rem(22);
  }
}

.table__header-br {
  @include mq(md) {
    display: none;
  }
}

.table__text {
  padding: 20px 0;
  border-bottom: 1px solid $c_key1;

  @include mq(md) {
    padding: 40px 30px;
  }
}

.table__title {
  margin-bottom: 10px;
  font-size: rem(18);
  font-weight: bold;
  color: $c_key1;

  @include mq(md) {
    margin-bottom: 15px;
    font-size: rem(22);
  }
}

.table__sentence {
  font-size: rem(13);
  line-height: 1.5;

  @include mq(md) {
    font-size: rem(15);
  }
}
